<template>
  <div rounded >
    <b-card :style="{margin:'5%'}">
    <b-form>
      <b-form-group
        id="id-input"
        label="username"
        label-for="input-id">
        <b-form-input
          id="input-id"
          v-model="form.username"
          required
          v-on:keypress.enter="submit"
          placeholder="유저네임을 입력해주세요.">
        </b-form-input>
      </b-form-group>
      <b-form-group
        id="password-input"
        label="password"
        label-for="input-password">
        <b-form-input
          id="input-password"
          v-model="form.password"
          required
          v-on:keypress.enter="submit"
          type="password"
          placeholder="비밀번호를 입력해주세요.">
        </b-form-input>
      </b-form-group>
    </b-form>
    <b-button @click="submit" class="float-right" type="submit" variant="primary">로그인</b-button>
    </b-card>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Home',
  components: {
    HelloWorld
  },
  methods:{
    test(){
      
    },
    submit(){
      var Inko = require('inko')
      var inko = new Inko()
      axios.post(this.backend + '/login', {
        username: this.form.username,
        password: inko.ko2en(this.form.password)
      })
      .then((res)=>{
        this.$store.state.isLogin = true
        this.$router.push({name:'Todo'})
      }
      ,(error) => {
        if (error.response.status === 500) {
            alert("유저네임과 비밀번호를 확인해주세요.")
        }
        else alert("잠시후 다시 시도해 주세요.")
      })

    }
  }
  ,
  data(){
    return{
      backend: 'http://27.96.130.157',
      form : {
        username: '',
        password: '',
      }
    }
  }
}
</script>
