<template>
  <div class="sticker">
    <b-input-group class="mb-3">

      <b-form-input v-model="title" placeholder="일정을 입력하세요."></b-form-input>
      <b-input-group-append>
        <b-form-datepicker
          class="mr-1"
          button-variant="danger"
          v-model="date"
          button-only
          right
          locale="ko-KR"
          aria-controls="example-input"
        ></b-form-datepicker>
      </b-input-group-append>
      <b-button @click="onSubmit()" variant="primary">등록</b-button>
    </b-input-group>
    <div></div>
  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import {eventBus} from '../main.js'
export default {
  name: 'HelloWorld',
  props:['msg'],
  data(){
    return{
      date: '',
      max: 100,
      backend: 'http://27.96.130.157/api',
      memo: '',
      title: '',
    }
  },
  methods:{
    signal(){
      eventBus.$emit('todoSubmit', new Date())
    },
    onSubmit(){
      
      axios.post(this.backend + "/todo",
      {
        title: this.title,
        'due_date' : this.date,
        memo : this.memo,
      })
      .then((res)=>{
        this.$store.state.submitTodo = true
        this.signal()
      })
    }
  }
}
</script>

