<template>
  <div class="sticker">
    <b-card-group columns>
      <b-card v-for="todo in todoList" :header="todo.day_left+'일전'" :bg-variant="todo.color" text-variant="white" class="text-center" align="center">
        <b-card-text>{{todo.title}}</b-card-text>
        <b-button @click="checkDone(todo._id)" pill variant="outline-light">완료</b-button>
      </b-card>
    </b-card-group>
  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import { eventBus } from '../main'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      value: 33.9874,
      max: 100,
      backend: 'http://27.96.130.157/api',
      todoList: '',
      bus:'asd',
    }
  },
  methods:{
    checkDone(id){
      axios.delete(this.backend+'/todo', {data : {_id:id}})
      .then((res)=>{
        this.getTodos()
      })
    },
    getTodos: function (){
      axios.get(this.backend + '/todo')
      .then((res)=>{
        this.todoList = res.data
        for(var i=0; i<this.todoList.length; i++){
          var now = new Date()
          var due = new Date(this.todoList[i].due_date)
          due.setHours(due.getHours()-9)
          var day_left =  Math.round((due-now)/1000/60/60/24)
          this.todoList[i].day_left =day_left
          
          if(day_left <= 1){
            this.todoList[i].color = "danger"
          }
          else if(day_left <= 3){
            this.todoList[i].color = "warning"
          }
          else if(day_left <= 5){
            this.todoList[i].color = "info"
          }
          else if(day_left <= 7){
            this.todoList[i].color = "success"
          }
          else if(day_left > 7){
            this.todoList[i].color = "primary"
          }
        }
      })
    },
    test: function (){
      console.log("TEST!!")
    }
  },
  created(){
    this.getTodos()
    eventBus.$on('todoSubmit', (date)=>{
      this.getTodos()
    })
  }
}
</script>

